'use client'; // Required for hooks like useState and useEffect

import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from '@/components/ui/accordion'; // Import from shadcn's location
import { useEffect, useState } from 'react';

// --- Type Definitions ---
interface Question {
  _id: string;
  title: string;
  urls: string[];
  difficulty: 'Easy' | 'Medium' | 'Hard';
}

interface Category {
  _id: string;
  title: string;
  questions: Question[];
}

// --- Helper Components ---

const LoadingSpinner = () => (
  <div className="flex flex-col items-center justify-center min-h-screen bg-background text-foreground">
    <div className="animate-spin rounded-full h-24 w-24 border-t-4 border-b-4 border-primary"></div>
    <p className="mt-6 text-xl tracking-wider">Fetching Ingredients...</p>
  </div>
);

const ErrorDisplay = ({ message }: { message: string }) => (
  <div className="flex items-center justify-center min-h-screen bg-background text-foreground p-6">
    <div className="bg-destructive border-2 border-destructive/50 rounded-lg p-8 text-center">
      <h2 className="text-3xl font-bold mb-4 text-destructive-foreground">
        Oops! Something went wrong.
      </h2>
      <p className="text-lg bg-destructive/80 p-3 rounded-md text-destructive-foreground">
        {message}
      </p>
      <p className="mt-6 text-md text-muted-foreground">
        Please ensure your backend server is running.
      </p>
    </div>
  </div>
);

const DifficultyTag = ({
  difficulty,
}: {
  difficulty: Question['difficulty'];
}) => {
  const styleMap = {
    Easy: 'bg-green-900/50 text-green-300 border-green-700',
    Medium: 'bg-yellow-900/50 text-yellow-300 border-yellow-700',
    Hard: 'bg-red-900/50 text-red-300 border-red-700',
  };
  const tagStyle =
    styleMap[difficulty] ||
    'bg-secondary text-secondary-foreground border-border';

  return (
    <span
      className={`px-3 py-1 text-sm font-medium rounded-full border ${tagStyle}`}
    >
      {difficulty}
    </span>
  );
};

const QuestionItem = ({ question }: { question: Question }) => (
  <a
    href={question.urls[0]}
    target="_blank"
    rel="noopener noreferrer"
    className="flex items-center justify-between p-4 bg-muted/40 hover:bg-muted/80 rounded-lg transition-colors duration-200"
  >
    <span className="text-card-foreground text-base md:text-lg">
      {question.title}
    </span>
    <DifficultyTag difficulty={question.difficulty} />
  </a>
);

// --- Main Page Component ---
export default function HomePage() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/v1/content');
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        const result = await response.json();
        setCategories(result.data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  if (loading) return <LoadingSpinner />;
  if (error) return <ErrorDisplay message={error} />;

  return (
    <main className="min-h-screen bg-background text-foreground font-sans p-4 sm:p-6 md:p-8">
      <div className="max-w-4xl mx-auto">
        <header className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-extrabold tracking-tight">
            GeekHeaven DSA Sheet
          </h1>
          <p className="text-muted-foreground mt-3 text-lg">
            Your curated list of essential DSA problems.
          </p>
        </header>

        <Accordion type="single" collapsible className="w-full">
          {categories.map((category) => (
            <AccordionItem value={category._id} key={category._id}>
              <AccordionTrigger className="text-xl hover:no-underline text-primary">
                {category.title}
              </AccordionTrigger>
              <AccordionContent>
                <div className="space-y-3 pt-2">
                  {category.questions.map((question) => (
                    <QuestionItem key={question._id} question={question} />
                  ))}
                </div>
              </AccordionContent>
            </AccordionItem>
          ))}
        </Accordion>

        <footer className="text-center mt-16 text-muted-foreground">
          <p>Built for the WebD Selection Task 2025-26.</p>
        </footer>
      </div>
    </main>
  );
}
